import jsonp from 'jsonp'
import semver from '@/utils/semver'
import { mapGetters } from 'vuex'
import UplusApi from '@uplus/uplus-api';
import config from '@/config'
const instance = new UplusApi();
instance.initDeviceReady()

let clearsession = false;
let isClosewebview = 0; // closewebview

// const defaultBindCard = [
//   { bankCode: '01050000', bankName: '' },
//   { bankCode: '03080000', bankName: '' },
//   { bankCode: '03010000', bankName: '' },
//   { bankCode: '01040000', bankName: '' }
// ]

export default {
  name: 'cashier',
  data() {
    return {
      skeletonLoading: true,

      payMapText: {
        ali_pay: '',
        pcredit: '',
        ali_huabei: '',
        ccb_fenqi: '',
        ceb_fenqi: '',
        acp_pay: '',
        acp_apple: 'Apple Pay',
        acp_android: 'Pay',
        wx_pay: '',
        kjt_pay: '',
        sg_ious: '',
        epay: '',
        bank_pay: '',
        b2b_pay: '',
        digiccy: '',
        aggre_pay: '',
        cash_card: '',
      },

      isWeikuan: false, // 
      leftTime: '', // 
      price: '', // 
      totalAmount: '', // 
      curSelected: '', // 
      curSelBank: '', // 
      curSelBankId: '', // id
      // curSelectedCodes: '', // codes
      orderId: '', // id
      orderSn: '', // 
      minPaymentAmt: '', // 

      paymethodsTopVOList: [],
      paymethodsMiddleVOList: [],
      paymethodsBottomVOList: [],

      cardlists: [], // 

      initLinkType: 0, //  (1) (0)
      linkType: 0, //  0:  1:
      platformCode: '', // 
      platformParentCode: '', // 

      stagepCreditId: '', // id
      stageIndex: 0, // 
      curClickStage: '', // 
      stageNum: '', // 
      stagecfgMark: '', //  mark
      discountPrice: 0, // 

      isToggle: false, // 
      unionPayFormData: {}, // 

      aliPayFormData: {}, // 
      weixinFormData: {}, // 
      epayFormData: {}, // 
      digiccyFormData: {}, // 

      isPaying: false, // 
      isSubmiting: false, // 

      isCanShowVersion: false, // 
      isShowEPay: true, // 
      isShowAlipay: true, // 
      aliPayType: 'h5', // 

      showMoreCard: false, // 
      // isWeixin: util.isWeixin(), // 
      isIOS: util.isIos(), // ios
      isSyBirdFromH5: false, // 

      numKeybordShow: false, //
      // smsCode: [], // 
      mobileNo: '', // 
      bankTel: '', // 
      preGetTime: 0, // 
      resetGetSms: false, // 
      leaveOrderTip: false, // 

      curDigitCard: '', // 
      curDigitCardInfo: null, // 
      digitMoneyPop: false, // 
      digitMsgAuth: false, // 
      digitCardList: [], // 

    }
  },
  components: {
    Header,
    skeleton,
    CaptchaDialog,
    leaveTip
  },
  computed: {
    ...mapGetters('freebankcard', ['getBankStyleInfo']),
    ...mapGetters([
      'ucId',
      'accessToken',
      'mId',
      'getUserInfo'
    ])
  },
  filters: {
    priceFormate: function (num, bit = 2) {
      // var parts = Number(num).toFixed(bit).split('.')
      // parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, '')
      return Number(num).toFixed(bit)
    }
  },
  beforeRouteEnter(to, from, next) {
    // 
    if (from.name === 'orderList' || from.name === 'orderDetail') {
      clearsession = true
    } else {
      clearsession = false
    }
    console.log(from.fullPath, location.href)
    if (from.fullPath.indexOf('closewebview=1') !== -1 || location.href.indexOf('closewebview=1') !== -1) {
      isClosewebview = 1
    } else {
      isClosewebview = 0
    }

    next()
  },
  mounted() {
    this.orderSn = this.$route.params.orderSn
    let query = this.$route.query

    this.isSyBirdFromH5 = query.isHaierSyn || ''

    // 
    if (query.orderSource) {
      sessionStorage.setItem('cur_order_from', query.orderSource)
    }

    if (util.isWeixin() && query.code) {
      this.skeletonLoading = false
      this.isPaying = true
      this.orderId = query.orderId
      this.initWxPayWithCode(query)
      return;
    }

    if (query.isd && query.isd === '1') {
      this.$dialog.confirm({
        title: '',
        message: '1\n2',
        messageAlign: 'left',
        confirmButtonText: ''
      }).then(() => {
        this.$router.replace('/orderList?tagChecked=2&goback=2')
      }).catch(() => {
        // on cancel
      });
    }

    // APP 
    if (util.isHaierApp()) {
      instance.initDeviceReady().then(() => {
        // APP onresume
        instance.addResumeEventListener(() => {
          this.checkOrderStatus()
        }, 'onresume_key');

        console.log('===>version:')
        instance.upAppinfoModule.getAppInfo().then(result => {
          let appVersion = result.retData.appVersion
          let osVersion = result.retData.OSversion
          console.log(appVersion, osVersion, 'appVersion')

          // APP
          // APP
          if ((appVersion && semver.gte(appVersion, '6.14.0')) || util.isSyBird()) {
            console.log('')
            this.isCanShowVersion = true
          } else {
            console.log('')
            this.isCanShowVersion = false
          }

          // APPSDK
          // APP6.19.0SDK
          if ((appVersion && semver.gte(appVersion, '6.21.0')) || util.isSyBird()) {
            this.isShowAlipay = true
            this.aliPayType = 'sdk'
          } else if (appVersion && semver.gte(appVersion, '6.19.0') && this.isIOS) {
            this.isShowAlipay = true
            this.aliPayType = 'sdk'
          } else if (appVersion && semver.lt(appVersion, '6.19.0') && semver.gte(osVersion, '14.0.0') && util.isUplus()) {
            this.isShowAlipay = false
          } else {
            this.isShowAlipay = true
            this.aliPayType = 'h5'
          }

          // 6.17.0 IOS
          if (util.isUplus()) {
            if (appVersion && semver.gte(appVersion, '6.17.0')) {
              // this.isShowEPay = true
            } else {
              this.isShowEPay = !this.isIOS
            }
          }
        });
      })
    } else if (util.isWeixin()) {
      this.isCanShowVersion = true
    } else {
      this.isCanShowVersion = false;
    }

    this.fetchData()
  },
  beforeDestroy() {
    // APP onresume,
    instance.removeResumeEventListener('onresume_key')
  },
  methods: {
    /**
     * 
    */
    toService() {
      this.$store.dispatch('goPageByType', {
        type: 29,
        params: {
          sceneId: 'cj102'
        }
      })
    },
    /**
     * 
     */
    fetchBindBankCard() {
      this.$http.get(URL.BANKCARD_FREE_LIST).then(({ data }) => {
        let result = data.data;
        let query = this.$route.query

        let bindCard = result.map(item => {
          return {
            bankCode: item.bankCode,
            bankName: item.bankName,
            cardNo: item.cardNo.replace(/\*/g, ''),
            id: item.id,
            bankTel: item.bankTel,
            mobileNo: item.mobileNo,
            canSelect: true
          }
        })
        // let filterArr = defaultBindCard.filter(item => {
        //   let flag = true;
        //   bindCard.forEach(card => {
        //     if (card.bankCode === item.bankCode) {
        //       flag = false
        //       return false
        //     }
        //   })
        //   return flag
        // })
        // console.log(filterArr)
        // this.cardlists = [...bindCard, ...filterArr];

        // ui
        this.cardlists = [...bindCard];

        //  
        if (+query.addcard) {
          this.curSelected = 'bank_pay'
          this.curSelBank = this.cardlists[0].bankCode;
          this.curSelBankId = this.cardlists[0].id
        }
      })
    },

    //  
    toPayWay(responseData) {
      // 
      let url = `${URL.ORDER_200PAYCHECK}?orderSn=${this.orderSn}`
      this.$http.get(url).then(({ data }) => {
        if (data.success) {
          // 
          if (this.curSelected === 'pcredit' || this.curSelected === 'ali_huabei') {
            let pCreditId = ''
            if (this.curSelected === 'ali_huabei') {
              pCreditId = '&pCreditId=' + this.stagepCreditId
            }
            // window.location.href = URL.HB_PAY_H5 + '?orderSn=' + this.orderSn + pCreditId
            util.openNewWindow(URL.HB_PAY_H5 + '?callBackUrl=' + encodeURIComponent(`${config.HOST}/sgmobile/payState`) + '&merchantUrl=' + encodeURIComponent(`${config.HOST}/sgmobile/payState`) + '&orderSn=' + this.orderSn + pCreditId)
            return
          }

          // 
          if (this.curSelected === 'ali_pay') {
            // window.location.href = URL.ALIPAY_H5 + '?orderSn=' + this.orderSn;
            util.openNewWindow(URL.ALIPAY_H5 + '?callBackUrl=' + encodeURIComponent(`${config.HOST}/sgmobile/payState`) + '&merchantUrl=' + encodeURIComponent(`${config.HOST}/sgmobile/payState`) + '&orderSn=' + this.orderSn)
            return
          }

          // 
          if (this.curSelected === 'wx_pay') {
            if (util.isHaierApp()) { // 
              this.wxSDKAppPay()
            } else if (util.isWeixin()) { //  h5
              this.doGetWxCode();
            } else { // h5
              this.wxH5Pay();
            }
          }

          //   
          if (this.curSelected === 'ccb_fenqi' || this.curSelected === 'ceb_fenqi') {
            let params = {}
            params.num = this.stageNum || 1;
            params.channel = 'sg'
            params.payType = this.curSelected
            params.cfgMark = this.stagecfgMark || ''
            params.memberId = this.mId
            params.orderSn = this.orderSn

            let payUrl; let myUrlAddress = null;

            payUrl = URL.CCB_FENQI_W + '?from=sgmobile&orderSn=' + params.orderSn + '&num=' + params.num + '&channel=' + params.channel + '&payType=' + params.payType + '&callbackUrl=sgmobile/payState' + '&memberId=' + params.memberId + '&cfgMark=' + params.cfgMark;

            if (util.isProdEnv()) {
              payUrl = config.PHOST + payUrl
            }

            jsonp(payUrl, null, (err, res) => {
              if (err) return;
              if (res.errorCode !== 13 && res.success) {
                if (params.payType === 'ceb_fenqi') {
                  myUrlAddress = encodeURI(res.data.cebfenqi.actionUrl) + '?Plain=' + encodeURI(res.data.cebfenqi.plain) + '&Signature=' + res.data.cebfenqi.sign;
                } else {
                  let installNum = '';
                  if (res.data.ccbfenqi.INSTALLNUM) {
                    installNum = '&INSTALLNUM=' + res.data.ccbfenqi.INSTALLNUM;
                  }
                  myUrlAddress = res.data.ccbfenqi.actionUrl + '&REMARK1=' + res.data.ccbfenqi.REMARK1 + '&CLIENTIP=' + res.data.ccbfenqi.CLIENTIP + '&BRANCHID=' + res.data.ccbfenqi.BRANCHID +
                                     '&REMARK2=' + res.data.ccbfenqi.REMARK2 + '&TXCODE=' + res.data.ccbfenqi.TXCODE + '&REGINFO=' + res.data.ccbfenqi.REGINFO + '&CURCODE=' + res.data.ccbfenqi.CURCODE +
                                     '&GATEWAY=' + res.data.ccbfenqi.GATEWAY + '&PROINFO=' + res.data.ccbfenqi.PROINFO + '&MERCHANTID=' + res.data.ccbfenqi.MERCHANTID + installNum +
                                     '&ORDERID=' + res.data.ccbfenqi.ORDERID + '&POSID=' + res.data.ccbfenqi.POSID + '&PAYMENT=' + res.data.ccbfenqi.PAYMENT + '&MAC=' + res.data.ccbfenqi.MAC +
                                     '&TYPE=' + res.data.ccbfenqi.TYPE + '&TIMEOUT=' + res.data.ccbfenqi.TIMEOUT;
                  // console.log(myUrlAddress);
                }

                util.openNewWindow(myUrlAddress, true);
              }
            })
          }

          // 
          if (this.curSelected === 'sg_ious') {
            // if (this.totalAmount < 600) {
            //   this.$toast('600');
            //   return;
            // }

            if (util.isNull(this.ucId) || util.isNull(this.accessToken)) {
              this.$toast(',');
              this.isSubmiting = false
              return
            }
            let url = `${URL.WHITE_SHOWS_QUERY_STATUS}?from=sgmobile`
            this.$http.post(url, {
              userId: this.ucId,
              token: this.accessToken,
            }).then(({ data }) => {
              if (!data.success) {
                this.$toast(data.message)
                this.isSubmiting = false
                return
              }

              let status = Number(data.data.applyStatus)

              if (status === 2 || status === 3) {
                this.$router.push('/sgwhitepay')
              }
              if (status === 0) {
                this.$http.get(URL.WHITE_SHOWS_APPLY, { params: {
                  backUrl: window.location.href,
                  userId: this.ucId,
                  token: this.accessToken
                } }).then(res => {
                  let result = res.data
                  if (result.success) {
                    location.href = result.data.data.url
                  } else {
                    this.$toast(result.message)
                    this.isSubmiting = false
                  }
                })
              }
              if (status === 1) {
                let url = `${URL.WHITE_SHOWS_PAY}?from=sgmobile&userId=${this.ucId}&token=${this.accessToken}&orderSn=${this.orderSn}&resultUrl=${config.HOST}`
                this.$http.post(url).then(res => {
                  let result = res.data
                  if (result.success) {
                    this.isSubmiting = false
                    util.openNewWindow(result.data.redirectUrl, true)
                  } else {
                    this.$toast(result.message)
                    this.isSubmiting = false
                  }
                });
              }
            })
          }

          // 
          if (this.curSelected === 'kjt_pay') {
            this.$http.get(URL.EASYCONNECT_INIT).then(res => {
              let result = res.data
              if (result.data.kjtAccount && result.data.kjtAccount.memberKjtpayAccount) { // 
                util.openNewWindow('/v2/h5/pay/kjtpay/request.html?callbackUrl=' + encodeURIComponent(`${config.HOST}/sgmobile/payState?data={"is_success":"T","order_id": "${this.orderId}"}`) + '&orderSn=' + this.orderSn + '&kjtAcount=' + result.data.kjtAccount.memberKjtpayAccount);
                // util.openNewWindow('/v2/h5/pay/kjtpay/request.html?callbackUrl=' + encodeURIComponent(`${config.HOST}/sgmobile/orderPay?type=submit`) + '&orderSn=' + this.orderSn + '&kjtAcount=' + result.data.kjtAccount.memberKjtpayAccount);
              } else {
                this.$dialog.alert({
                  title: '',
                  confirmButtonText: '',
                }).then(() => {
                  this.$router.push('/bankcard')
                });
                this.isSubmiting = false
              }
            })
          }

          // 
          if (this.curSelected === 'acp_pay') {
            let url = `${URL.UNINO_PAY}?from=sgmobile&orderSn=${this.orderSn}&channel=sg&callbackUrl=${encodeURIComponent(`${config.HOST}/sgmobile/payState?data={"is_success":"T","order_id": "${this.orderId}"}`)}`
            // let url = `${URL.UNINO_PAY}?from=sgmobile&orderSn=${this.orderSn}&channel=sg&callbackUrl=${encodeURIComponent(`${config.HOST}/sgmobile/orderPay?type=submit`)}`

            if (util.isProdEnv()) {
              url = config.PHOST + url
            }
            jsonp(url, null, (err, res) => {
              if (err) return;
              if (res.success) {
                this.unionPayFormData = res.data
                this.$nextTick(() => {
                  this.isSubmiting = false
                  document.getElementById('unionPay_form').submit()
                })
              }
            })
          }
          this.$util.gioTrackWithVariable('MB13419', {
            user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
            member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
            order_id: this.orderSn ? this.orderSn + '' : 'null',
            par_value: this.totalAmount ? this.totalAmount + '' : 'null'
          })
        } else {
          this.$toast(data.message)
          this.isSubmiting = false
        }
      })
    },
    // 
    inDirectPayWay() {
      // 
      let url = `${URL.ORDER_200PAYCHECK}?orderSn=${this.orderSn}`
      this.$http.get(url, {
        cancelRepeat: false
      }).then(({ data }) => {
        if (data.success) {
          if (this.curSelected === 'ali_pay') { // 
            if (util.isHaierApp() && this.aliPayType === 'sdk') {
              this.checkPaySubmit({
                checkoutType: this.linkType,
                orderSn: this.orderSn, //		string
                payPartnerId: this.platformParentCode, // code	string
                payType: this.curSelected, // wx_payali_pay	string
                payTypeId: 500,	// code	string	json
                userId:	this.mId,
                apiVersion: 1,
              }).then(({ data }) => {
                let result = data;
                // console.log(result.data.data.zfb_data_package, 'result paysubmit')
                if (result.success) {
                  instance.initDeviceReady().then(() => {
                    instance.upAlipayModule.alipayOrder({ order: result.data.data.zfb_data_package }).then(ret => {
                      console.log('', ret)

                      this.$util.gioTrackWithVariable('pay_result', {
                        'pay_result_Mcode': ret.retData.resultStatus ? ret.retData.resultStatus + '' : 'null',
                        'pay_result_Message': ret.retData.memo || 'null',
                        'pay_result_platform': 'alipay',
                        'os': util.isIos() ? 'ios' : 'android',
                        'order_id': this.orderSn + ''
                      }).then(() => {
                        this.isSubmiting = false
                        if (ret.retData.resultStatus === '9000') {
                          // this.$router.replace('/orderPay?type=submit')
                          this.$router.replace('/payState?data={"is_success":"T","order_id": "' + this.orderId + '"}')
                        } else {
                          // this.$router.push('/payState?data={"is_success":"N","order_id": "' + this.orderId + '"}')
                        }
                      })
                    })
                  })
                } else {
                  this.$toast(result.message)
                  this.isSubmiting = false
                }
              })
            } else {
              this.checkPaySubmit({
                checkoutType: this.linkType,
                orderSn: this.orderSn, //		string
                payPartnerId: this.platformParentCode, // code	string
                payType: this.curSelected, // wx_payali_pay	string
                payTypeId: this.platformCode,	// code	string	json
                userId:	this.mId,
                apiVersion: 1,
              }).then(({ data }) => {
                let result = data;
                // console.log(result, 'result paysubmit')
                if (result.success) {
                  this.aliPayFormData = result.data.data
                  // console.log(this.aliPayFormData)
                  this.$nextTick(() => {
                    this.isSubmiting = false
                    document.getElementById('aliPay_form').submit()
                  })
                } else {
                  this.$toast(result.message)
                  this.isSubmiting = false
                }
              })
            }
          } else if (this.curSelected === 'wx_pay' && util.isWeixin()) { // 
            this.doGetWxCode();
          } else if (this.curSelected === 'wx_pay') {
            if (util.isHaierApp()) { // APP
              console.log(' app sdk')

              instance.upUserModule.checkAppIsInstalled({ platform: 'wechat' }).then((rep) => {
                if (rep.retData && rep.retData.status) {
                  this.checkPaySubmit({
                    checkoutType: this.linkType,
                    orderSn: this.orderSn, //		string
                    payPartnerId: '201', // code	string
                    payType: this.curSelected, // wx_payali_pay	string
                    payTypeId: '102',	// code	string	json
                    userId:	this.mId,
                    from: this.$util.isSyBird() ? '' : '1',
                    type: this.$util.isSyBird() ? 'syn' : 'app',
                    apiVersion: 1,
                  }).then(result => {
                    console.log(result);
                    if (!result.data.success) {
                      this.$toast(result.data.message)
                      this.isSubmiting = false
                      return;
                    }

                    let appInfo = result.data && result.data.data && result.data.data.data
                    let appInfoJson = this.$util.strJSONParse(appInfo.wx_data_package, {});
                    console.log(appInfoJson, 'appInfoJson');

                    this.uplusAppWxPay({
                      // partnerid: appInfoJson.partnerid,
                      prepayid: appInfoJson.prepayid,
                      noncestr: appInfoJson.noncestr,
                      timestamp: appInfoJson.timestamp,
                      sign: appInfoJson.sign,
                    }).then(res => {
                      console.log(res, 'sdk ')
                      this.$util.gioTrackWithVariable('pay_result', {
                        'pay_result_Mcode': (res.retCode || res) || 'null',
                        'pay_result_Message': res.retInfo || 'null',
                        'pay_result_platform': 'wx',
                        'os': util.isIos() ? 'ios' : 'android',
                        'order_id': this.orderSn
                      }).then(() => {
                        this.isSubmiting = false
                        if (res.retCode === '000000') {
                          // this.$router.replace('/orderPay?type=submit')
                          this.$router.replace('/payState?data={"is_success":"T","order_id": "' + this.orderId + '"}')
                        } else {
                          // this.$router.push('/payState?data={"is_success":"N","order_id": "' + this.orderId + '"}')
                        }
                      })
                    })
                  })
                } else {
                  this.$toast('')
                  this.isSubmiting = false
                }
              })
            } else { // H5
              this.wxH5Pay()
            }
          } else if (this.curSelected === 'digiccy') {
            console.log('');
            // 
            this.findDigitMoneyCardList();
          } else if (this.curSelected === 'sg_ious') {
            // if (this.totalAmount < 600) {
            //   this.$toast('600');
            //   return;
            // }

            if (util.isNull(this.ucId) || util.isNull(this.accessToken)) {
              this.$toast(',');
              this.isSubmiting = false
            }
            console.log('')
            let urlToken = URL.WHITE_ACCESS_TOKEN;
            this.$http.post(urlToken, {
            }).then(({ data }) => {
              if (data.success) {
                let accessToken = data.data;
                this.$store.commit('setAcsToken', accessToken, { root: true });
                window.localStorage.setItem('accessToken', accessToken);

                let url = `${URL.WHITE_SHOWS_QUERY_STATUS}?from=sgmobile`
                this.$http.post(url, {
                  userId: this.ucId,
                  token: accessToken,
                }).then(({ data }) => {
                  if (!data.success) {
                    this.$toast(data.message)
                    this.isSubmiting = false
                    return
                  }

                  let status = Number(data.data.applyStatus)
                  // status = 1
                  if (status === 2 || status === 3) {
                    this.$router.push('/sgwhitepay')
                  }
                  if (status === 0) {
                    this.$http.get(URL.WHITE_SHOWS_APPLY, { params: {
                      backUrl: window.location.href,
                      userId: this.ucId,
                      token: accessToken
                    } }).then(res => {
                      let result = res.data
                      if (result.success) {
                        location.href = result.data.data.url
                      } else {
                        this.$toast(result.message)
                        this.isSubmiting = false
                      }
                    })
                  }
                  if (status === 1) {
                    this.checkPaySubmit({
                      checkoutType: this.linkType,
                      orderSn: this.orderSn, //		string
                      payPartnerId: this.platformParentCode, // code	string
                      payType: 'bt_pay', //	string
                      payTypeId: this.platformCode,	// code	string	json
                      userId:	this.mId,
                      ptUserId: accessToken,
                      apiVersion: 1,
                    }).then(({ data }) => {
                      let result = data;
                      console.log(result, ' paysubmit')
                      if (!result.success) {
                        this.$toast(result.message);
                        this.isSubmiting = false
                        return
                      }
                      location.href = result.data.data.url
                    })
                  }
                })
              } else {
                if (data.errorCode === '-100') {
                  // 
                  this.$store.dispatch('doLogin');
                } else if (data.errorCode === '1009') {
                  // 
                  this.$router.push('/sgwhitepay');
                }
              }
            })
          } else if (this.curSelected === 'epay') {
            console.log(' ')
            this.checkPaySubmit({
              checkoutType: this.linkType,
              orderSn: this.orderSn, //		string
              payPartnerId: this.platformParentCode, // code	string
              payType: 'epay', //	string
              payTypeId: this.platformCode,	// code	string	json
              userId:	this.mId,
              // ptUserId: this.accessToken,
              apiVersion: 1,
            }).then(({ data }) => {
              let result = data;
              if (result.success) {
                this.epayFormData = result.data.data
                console.log(this.epayFormData, 'epayFormData')
                // sessionStorage.setItem('epayFormData', JSON.stringify({
                //   ...this.epayFormData
                // }));
                // this.$nextTick(() => {
                //   this.$router.replace('/orderpaymiddle')
                // })

                if (util.isHaierApp()) {
                  if (util.isIos()) {
                    this.$http.post(URL.GET_CHANGE_WEBVIEW_DATA, {
                      key: this.orderSn,
                      obj: this.epayFormData,
                      method: 'set'
                    }).then(res => {
                      console.log(res, 'aaaaaaaa')
                      let result = res.data
                      if (result.success) {
                        let targetUrl = config.HOST + '/sgmobile/orderpaymiddle?container_type=4&hidesBottomBarWhenPushed=1&orderSn=' + this.orderSn
                        console.log(targetUrl, 'targetUrl')
                        instance.initDeviceReady().then(() => {
                          instance.upVdnModule.goToPage({ url: targetUrl });
                        });
                      }
                    })
                  } else {
                    // sessionStorage.setItem('epayFormData', JSON.stringify({
                    //   ...this.epayFormData
                    // }));
                    // this.$nextTick(() => {
                    //   this.$router.replace('/orderpaymiddle')
                    // });
                    this.$nextTick(() => {
                      this.isSubmiting = false
                      document.getElementById('epay_form').submit()
                    })
                  }
                } else {
                  this.$nextTick(() => {
                    this.isSubmiting = false
                    document.getElementById('epay_form').submit()
                  })
                }
              } else {
                this.$toast(result.message)
                this.isSubmiting = false
              }
            })
          } else if (this.curSelected === 'bank_pay') {
            console.log('')
            // this.isSubmiting = true
            this.sendSmsCaptch()
          } else if (this.curSelected === 'b2b_pay') {
            this.b2bPaySubmit()
          } else if (this.curSelected === 'aggre_pay' || this.curSelected === 'cash_card') {
            this.$router.push('/scanpay/' + this.orderSn + '?type=' + this.curSelected)
          } else {
            this.$toast('')
            this.isSubmiting = false
          }
          this.$util.gioTrackWithVariable('MB13419', {
            user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
            member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') + '' : 'null',
            order_id: this.orderSn ? this.orderSn + '' : 'null',
            par_value: this.totalAmount ? this.totalAmount + '' : 'null'
          })
        } else {
          this.$toast(data.message)
          this.isSubmiting = true
        }
      });
    },

    b2bPaySubmit() {
      this.checkPaySubmit({
        checkoutType: this.linkType,
        orderSn: this.orderSn, //		string
        payPartnerId: this.platformParentCode, // code	string
        payType: 'b2b_pay', //	string
        payTypeId: this.platformCode, // code	string	json
        userId: this.mId,
        // ptUserId: this.accessToken,
        apiVersion: 1,
      }).then(({
        data
      }) => {
        let result = data;
        if (result.success) {
          this.$router.push(`/orderCashierB2BPay/${encodeURIComponent(result.data.data.url)}`);
        } else {
          this.$dialog.alert({
            message: data.message,
            className: 'dialog-custom',
          });
          this.isSubmiting = false
        }
      })
    },

    checkPaySubmit(options) {
      let url = `${URL.TO_ORDER_PAY_NEW}`
      return this.$http.post(url, options, {
        cancelRepeat: false
      })
    },

    toSubmit() {
      this.$util.gioTrackWithVariable('choose_payment_method', {
        'payment_method': (this.payMapText[this.curSelected]) || 'null',
        'orderId': this.orderSn
      }).then(() => {
        if (this.isSubmiting) return
        // this.isSubmiting = true
        // this.linkType = 0

        if (!this.linkType) { //  0
          // this.toPayWay()
          this.reportPayLog()
        } else { // 
          this.inDirectPayWay()
        }
      })
    },

    // 
    changeStageFn(item, index) {
      this.stageIndex = index;
      this.curSelected = item.code
      // this.curSelectedCodes = item.middlePlatformCodes
      this.curClickStage = item.code
      this.minPaymentAmt = item.minPaymentAmt
      item.stagesrepaidTotalAmt = item.stagesVOList[index].repaidTotalAmt
      this.stagepCreditId = item.stagesVOList[index].pCreditId
      this.price = item.stagesVOList[index].eachPeriodAmt
      this.stageNum = item.stagesVOList[index].num
      this.stagecfgMark = item.cfgMark
    },
    // 
    changeSelected(item) {
      if (item.payShowType !== '1') return
      if (this.curSelected !== item.code) {
        this.stageIndex = 0;
        if (item.stagesVOList.length) {
          item.stagesrepaidTotalAmt = item.stagesVOList[0].repaidTotalAmt
          this.stagepCreditId = item.stagesVOList[0].pCreditId
        }
      }
      this.linkType = item.checkoutType
      this.platformCode = item.middlePlatformCode
      this.platformParentCode = item.middlePlatformParentCode

      this.stagecfgMark = item.cfgMark
      this.curSelected = item.code
      // this.curSelectedCodes = item.middlePlatformCodes
      this.curClickStage = item.code
      this.discountPrice = Number(item.discountAmt || null)
      this.minPaymentAmt = item.minPaymentAmt
      this.isSubmiting = false
      sessionStorage.setItem('cur_pay_method', JSON.stringify({ [this.orderSn]: item }))
    },
    readyInit() {
      this.paymethodsTopVOList.forEach(item => {
        item.stagesrepaidTotalAmt = item.stagesVOList.length ? item.stagesVOList[0].repaidTotalAmt : ''
        // 
        if (item.code === 'digiccy') {
          this.digitCardList = item.digiccyInfoResponseList
        }
        // 
        // if (item.code === 'wx_pay' && this.linkType === 0 && !util.isWeixin()) {
        //   this.hasinDirectWxPay = true
        // } else if (item.code === 'wx_pay' && this.linkType === 0 && util.isWeixin()) {
        //   this.hasinDirectWxPay = false
        // }
      })
      this.paymethodsMiddleVOList.forEach(item => {
        item.stagesrepaidTotalAmt = item.stagesVOList.length ? item.stagesVOList[0].repaidTotalAmt : ''
        // 
        if (item.code === 'digiccy') {
          this.digitCardList = item.digiccyInfoResponseList
        }
        // 
        // if (item.code === 'wx_pay' && this.linkType === 0 && !util.isWeixin()) {
        //   this.hasinDirectWxPay = true
        // } else if (item.code === 'wx_pay' && this.linkType === 0 && util.isWeixin()) {
        //   this.hasinDirectWxPay = false
        // }
      })
      this.paymethodsBottomVOList.forEach(item => {
        item.stagesrepaidTotalAmt = item.stagesVOList.length ? item.stagesVOList[0].repaidTotalAmt : ''
        // 
        if (item.code === 'digiccy') {
          this.digitCardList = item.digiccyInfoResponseList
        }
        // 
        // if (item.code === 'wx_pay' && this.linkType === 0 && !util.isWeixin()) {
        //   this.hasinDirectWxPay = true
        // } else if (item.code === 'wx_pay' && this.linkType === 0 && util.isWeixin()) {
        //   this.hasinDirectWxPay = false
        // }
      })

      this.linkType = this.initLinkType = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].checkoutType : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].checkoutType : this.paymethodsBottomVOList[0].checkoutType

      this.platformCode = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].middlePlatformCode : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].middlePlatformCode : this.paymethodsBottomVOList[0].middlePlatformCode

      this.platformParentCode = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].middlePlatformParentCode : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].middlePlatformParentCode : this.paymethodsBottomVOList[0].middlePlatformParentCode

      this.curSelected = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].code : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].code : this.paymethodsBottomVOList[0].code

      // ios14app 6.19 
      if (!this.isShowAlipay) {
        this.curSelected = 'wx_pay'
      }

      // this.curSelectedCodes = this.paymethodsTopVOList[0].middlePlatformCodes
      this.minPaymentAmt = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].minPaymentAmt : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].minPaymentAmt : this.paymethodsBottomVOList[0] ? this.paymethodsBottomVOList[0].minPaymentAmt : ''

      let oldPayMethod = this.$util.strJSONParse(sessionStorage.getItem('cur_pay_method'), {})
      if (oldPayMethod && oldPayMethod[this.orderSn] && !clearsession) {
        let json = oldPayMethod[this.orderSn]
        if (json.code === 'bank_pay') {
          this.curSelected = json.code
          this.curSelBank = json.bankCode
          this.curSelBankId = json.id
          this.mobileNo = json.mobileNo
          this.bankTel = json.bankTel
          this.resetGetSms = true
        } else {
          this.linkType = json.checkoutType
          this.platformCode = json.middlePlatformCode
          this.platformParentCode = json.middlePlatformParentCode
          this.stagecfgMark = json.cfgMark
          this.curSelected = json.code
          this.curClickStage = json.code
          this.discountPrice = Number(json.discountAmt || null)
          this.minPaymentAmt = json.minPaymentAmt
          this.isSubmiting = false
        }
      }
    },
    fetchData() {
      let env = util.isWeixin() ? '4' : '2'

      let url = `${URL.TO_ORDER_SUBMIT_SUCCESS_NEW}?channel=sg&terminalId=${env}&orderNo=${this.orderSn}`
      this.$http.post(url).then(res => {
        this.skeletonLoading = false
        if (res.data.success) {
          let redata = res.data.data;
          this.price = this.totalAmount = redata.orderAmt.toFixed(2)
          this.isWeikuan = redata.orderType === 2
          if (redata.remainTime < 0 && !this.isWeikuan && redata.orderType !== 22) {
            this.$router.push('/payState')
            // this.$router.replace('/orderPay?type=submit')
            return
          }
          if (!this.isWeikuan) {
            this.intervalLeft(redata.remainTime)
          }
          this.orderId = redata.orderId
          this.paymethodsTopVOList = redata.paymethodsTopVOList
          this.paymethodsMiddleVOList = redata.paymethodsMiddleVOList
          this.paymethodsBottomVOList = redata.paymethodsBottomVOList
          this.readyInit()

          // 
          this.fetchBindBankCard()
        } else {
          this.$toast(res.data.message)
        }
      })
    },
    checkOrderStatus() {
      let env = util.isWeixin() ? '4' : '2'

      let url = `${URL.TO_ORDER_SUBMIT_SUCCESS_NEW}?channel=sg&terminalId=${env}&orderNo=${this.orderSn}`
      this.$http.post(url).then(res => {
        if (res.data.success && res.data.data && res.data.data.payStatus === 101) { // 100101
          // this.$router.replace('/orderList?tagChecked=2')
          this.$router.replace('/payState?data={"is_success":"T","order_id": "' + this.orderId + '"}')
        }
      })
    },

    // changePlatformCode(platformCode) {
    //   const platformCodesArr = this.curSelectedCodes.split(',');
    //   platformCodesArr.forEach(item => {
    //     if (item.indexOf(platformCode) !== -1) {
    //       this.platformParentCode = item.split('-')[0]
    //     }
    //   })
    //   console.log(this.platformParentCode)
    // },

    uplusAppWxPay(options) {
      return new Promise((resolve, reject) => {
        instance.upWeiXinModule.pullUpPaymentFlow({
          // partnerid: options.partnerid,
          prepayid: options.prepayid,
          noncestr: options.noncestr,
          timestamp: options.timestamp,
          sign: options.sign,
        }).then(res => {
          resolve(res)
        }).catch(() => {
          resolve('400')
        });
      })
    },
    //  APP sdk   from=1 app
    wxSDKAppPay() {
      instance.upUserModule.checkAppIsInstalled({ platform: 'wechat' }).then((rep) => {
        if (rep.retData && rep.retData.status) {
          this.$http.get(`${URL.WX_PAY_APP}?orderSn=${this.orderSn}&from=1`).then(res => {
            let result = res.data;
            console.log(result.data, '  APP sdk ');
            if (result.success) {
              this.uplusAppWxPay({
                prepayid: result.data.prepayid,
                noncestr: result.data.noncestr,
                timestamp: result.data.timestamp,
                sign: result.data.sign,
              }).then(res => {
                console.log(res, ' APP sdk ')
                if (res.retCode === '000000') {
                  this.$router.replace('/orderList?tagChecked=2')
                } else {
                  this.$router.replace('/orderList?tagChecked=1')
                }
              })
            } else {
              this.$toast(result.message)
              this.isSubmiting = false
            }
          })
        } else {
          this.$toast('')
          this.isSubmiting = false
        }
      });
    },

    // H5
    wxH5Pay() {
      if (!this.linkType) { // 
        this.$http.get(`${URL.WX_PAY_H5}?orderSn=${this.orderSn}`).then(res => {
          let result = res.data;
          console.log(result, '=== h5 wxpay ===');
          if (result.success) {
            if (result.data.returnCode === 'SUCCESS' && result.data.resultCode === 'SUCCESS') {
              util.openNewWindow(result.data.mwebUrl, true)
            } else {
              this.$toast(result.data.returnMsg)
              this.$router.replace('/orderList?tagChecked=1')
            }
          } else {
            this.$toast(result.message)
            this.isSubmiting = false
          }
        })
      } else {
        this.checkPaySubmit({
          checkoutType: this.linkType,
          orderSn: this.orderSn, //		string
          payPartnerId: this.platformParentCode, // code	string
          payType: this.curSelected, // wx_payali_pay	string
          payTypeId: this.platformCode,	// code	string	json
          userId:	this.mId,
          type: 'h5',
          apiVersion: 1,
        }).then(({ data }) => {
          let result = data;
          console.log(result, 'H5')
          if (result.success) {
            this.weixinFormData = result.data.data
            console.log(this.weixinFormData)
            // this.$nextTick(() => {
            //   document.getElementById('weixin_form').submit()
            // })
            this.isSubmiting = false
          } else {
            this.$toast(result.message)
            this.isSubmiting = false
          }
          // util.openNewWindow(result.data.data.url, true)
        })
      }
    },

    // code
    doGetWxCode() {
      if (!this.linkType) { // 
        console.log('  ')
        let redirectUrl = location.href.indexOf('?') !== -1 ? location.href + '&linkType=0' : location.href + '?linkType=0';
        util.getWxCode('wxb03a523baf487993', redirectUrl);
        // location.href = '/v2/h5/pay/wxpay.html?orderSn=' + this.orderSn + '&openid=' + openid
      } else { // 
        let redirectUrl;
        let queryParams = {};
        queryParams.checkoutType = this.linkType;
        queryParams.orderSn = this.orderSn; //		string
        queryParams.payPartnerId = this.platformParentCode; // code	string
        queryParams.payType = this.curSelected; // wx_payali_pay	string
        queryParams.payTypeId = this.platformCode;	// code	string	json
        queryParams.userId = 	this.mId;
        queryParams.linkType = 1
        queryParams.orderId = this.orderId
        let query = this.serializParams(queryParams);

        redirectUrl = location.href.indexOf('?') !== -1 ? location.href + '&' + query : location.href + '?' + query;
        util.getWxCode('wxb03a523baf487993', redirectUrl);
      }
    },

    initWxPayWithCode(query) {
      this.$http.get(URL.WX_CODE_TO_PAYINFO, { params: { code: query.code } }).then(({ data }) => {
        let result = data.data
        console.log(result)
        console.log(query, '====query====')
        if (!+query.linkType) { // 
          console.log('  openid', result.openid, this.orderSn);
          this.reportPayLog();
          //  openid
          //  location.href = '/v2/h5/pay/wxpay/pay.html?orderSn=' + this.orderSn + '&openid=' + result.openid
          // this.$http.get(`${URL.WX_PAY_INWXH5}?orderSn=${this.orderSn}&openid=${result.openid}&from=2`).then(({ data }) => {
          //   let resInfo = data.data;
          //   console.log(resInfo, '   sdk ');
          //   util.InWXH5Pay({
          //     appId: resInfo.appId,
          //     timeStamp: resInfo.timeStamp,
          //     nonceStr: resInfo.nonceStr,
          //     package: resInfo.package,
          //     signType: resInfo.signType,
          //     paySign: resInfo.paySign,
          //     success: () => {
          //       //  
          //       this.checkOrderOriginUrl();
          //     },
          //     fail: (err) => {
          //       console.log(err)
          //       this.$toast('')
          //       history.back()
          //     }
          //   })
          // })
        } else { // 
          this.checkPaySubmit({
            checkoutType: query.checkoutType,
            orderSn: query.orderSn, //		string
            payPartnerId: query.payPartnerId, // code	string
            payType: query.payType, // wx_payali_pay	string
            payTypeId: query.payTypeId,	// code	string	json
            userId:	query.userId,
            ptUserId: result.openid,
            type: 'official',
            apiVersion: 1,
          }).then(({ data }) => {
            let response = data;
            console.log(response, 'response')
            if (response.success) {
              let resInfo = this.$util.strJSONParse(response.data.data.wx_data_package, {})
              console.log(resInfo, 'resInfo resInfo')
              util.InWXH5Pay({
                appId: resInfo.appid,
                timeStamp: resInfo.timestamp,
                nonceStr: resInfo.noncestr,
                package: resInfo.package,
                // signType: response.data.signType,
                paySign: resInfo.sign,
                success: () => {
                  this.$router.replace('/payState?data={"is_success":"T","order_id": "' + this.orderId + '"}')
                  //  
                  // this.checkOrderOriginUrl();
                },
                fail: (err) => {
                  console.log(err)
                  this.$toast('')
                  history.back()
                }
              })
            } else {
              this.$toast(response.message);
            }
          })
        }
      })
    },

    //  
    checkOrderOriginUrl() {
      this.$http.get(URL.GetExtendInfoByOrderId, { params: { 'orderId': this.orderId } }).then(({ data }) => {
        console.log(data, '')
        if (data.data && data.data.returnUrl) {
          // location.href = data.data.returnUrl
          // this.$router.replace('/orderPay?type=submit')
          this.$router.replace('/payState?data={"is_success":"T","order_id": "' + this.orderId + '"}')
        } else {
          // this.$router.replace('/orderList?tagChecked=2&goback=2')
          this.$router.replace('/payState')
        }
      })
    },

    /**
     * 
     */
    reportPayLog() {
      this.$http.post(URL.ORDER_ERROR_LOG, {
        json: {
          orderId: this.orderId,
          linkType: this.linkType,
          curSelected: this.curSelected,
          paymethodsTopVOList: this.paymethodsTopVOList,
          paymethodsMiddleVOList: this.paymethodsMiddleVOList,
          paymethodsBottomVOList: this.paymethodsBottomVOList,
        }
      })
      this.$dialog.confirm({
        // allowHtml: true,
        title: '',
        message: ' <a href="tel:4008287777" style="color: #2979FF">4008287777</a>',
      }).then(() => {
        // on confirm
        location.href = 'tel:4008287777'
        this.isSubmiting = false
      }).catch(() => {
        // on cancel
        this.isSubmiting = false
      });
    },

    intervalLeft(left) {
      let run = () => {
        if (left < 0) {
          left = 0;
          clearInterval(timer);
          return;
        }
        var hour = Math.floor(left / 3600);
        var min = Math.floor((left % 3600) / 60);
        var sec = Math.floor((left % 3600) % 60);
        left--;
        this.leftTime = util.numberToDouble(hour) + ':' + util.numberToDouble(min) + ':' + util.numberToDouble(sec)
      }
      let timer = setInterval(run, 1000);
      run();
    },
    toggleShowWay() {
      this.isToggle = !this.isToggle
    },

    serializParams(obj) {
      var str = ''; var index = 0
      for (let key in obj) {
        str += index ? '&' + key + '=' + obj[key] : key + '=' + obj[key]
        index++;
      }
      return str;
    },

    /**
     * 
     */
    addCard() {
      this.$router.push('/freeapply?addcard=' + this.orderSn);
    },

    /**
     * 
     */
    changeBankSelect(item) {
      console.log(item)
      if (!item.canSelect) {
        this.addCard()
        return
      }
      item.code = 'bank_pay'
      this.curSelected = item.code
      this.curSelBank = item.bankCode
      this.curSelBankId = item.id
      this.mobileNo = item.mobileNo
      this.bankTel = item.bankTel
      this.resetGetSms = true
      sessionStorage.setItem('cur_pay_method', JSON.stringify({ [this.orderSn]: item }))
    },

    /**
     * 
     */
    showMore() {
      this.showMoreCard = !this.showMoreCard
    },

    /**
     * 
     */
    sendSmsCaptch() {
      this.numKeybordShow = true;
      if (Date.now() - this.preGetTime <= 60000 && !this.resetGetSms) {
        return;
      }
      this.$http.post(URL.sendSmsCapthNew, {
        id: this.curSelBankId,
      }).then(res => {
        let result = res.data;
        console.log(result);
        if (result.success) {
          this.preGetTime = Date.now();
          this.resetGetSms = false
        } else {
          // this.isSubmiting = false
          this.$toast(result.message)
        }
      })
    },

    // 
    onSubmitCaptcha(value) {
      // this.asynSubmitOrder();
      this.checkPaySubmit({
        orderSn: this.orderSn, //		string
        payType: 'bind_card_pay', //	string
        smsCode: value,
        checkoutType: 1,
        payTypeId: 5070,	// code	string	json
        payPartnerId: 201, // code	string
        userId:	this.mId,
        noCardPaySignId: this.curSelBankId,
      }).then(({ data }) => {
        let result = data;
        if (!result.success) {
          console.log(data)
          this.$toast(result.message);
          // if (data.errorCode === 'PCWB0001') {
          //   // this.smsCode = [];
          //   this.$toast(result.message);
          // } else {
          //   this.$router.replace('/payState')
          // }
        } else {
          this.$util.gioTrackWithVariable('MB13419', {
            user_id: localStorage.getItem('ucId') ? localStorage.getItem('ucId') + '' : 'null',
            member_id: localStorage.getItem('mId') ? localStorage.getItem('mId') : 'null',
            order_id: this.orderSn ? this.orderSn : 'null',
            par_value: this.totalAmount ? this.totalAmount : 'null'
          })
          // this.$router.replace('/orderPay?type=submit')
          this.$router.replace('/payState?data={"is_success":"T","order_id": "' + this.orderId + '"}')
        }
      })
    },
    onCloseCaptchaDialog() {
      this.numKeybordShow = false;
      this.digitMsgAuth = false;
      this.isSubmiting = false
    },

    /**
     * 
     */
    goback() {
      this.leaveOrderTip = true
      // let hour = this.leftTime.split(':')[0]
      // let min = this.leftTime.split(':')[1]
      // let html;
      // if (this.isWeikuan) {
      //   html = '<p style="color: #666"></p>'
      // } else {
      //   html = '<p style="color: #666">' + hour + '' + min + '</p>'
      // }
      // this.$dialog.confirm({
      //   title: '',
      //   message: html,
      //   confirmButtonText: '',
      //   cancelButtonText: '',
      //   cancelButtonColor: '#999',
      //   messageAlign: 'left'
      // }).then(() => {
      //   // on confirm
      // }).catch(() => {
      //   this.$router.replace(isClosewebview ? '/orderList?closewebview=1' : '/orderList')
      // });
    },
    doBack() {
      if (this.$util.isSyBird() || +this.isSyBirdFromH5) {
        // location.href = config.SYNHOST + '/common/site/pay?area=mobile&keysword=&platform=Wap&controller=member&action=orders&orderStatus=0'
        this.$store.dispatch('doHistoryBack')
      } else if (this.$util.isHaierApp()) {
        this.$router.replace(isClosewebview ? '/orderList?closewebview=1' : '/orderList')
      } else {
        this.$store.dispatch('doHistoryBack')
      }
    },

    /**
     * 
     */
    findDigitMoneyCardList() {
      // this.$http.get().then(() => {
      // this.digitCardList = []
      this.digitMoneyPop = true;
      this.curDigitCardInfo = this.digitCardList[0]
      this.curDigitCard = this.curDigitCardInfo.id
      // })
    },

    /**
     * 
     */
    changeDigitCard(item) {
      this.curDigitCard = item.id
      this.curDigitCardInfo = item;
    },

    /**
     * 
     */
    doConfirmDigitCard() {
      this.digitMoneyPop = false;
      this.digitMsgAuth = true
      this.digitPayMsgFn();
    },

    /**
     * 
     */
    digitPaySubmit(msgValue) {
      console.log('digitPaySubmit', msgValue)
      this.checkPaySubmit({
        checkoutType: this.linkType,
        orderSn: this.orderSn, //		string
        payPartnerId: this.platformParentCode, // code	string
        payType: this.curSelected, // wx_payali_pay	string
        payTypeId: this.platformCode,	// code	string	json
        userId:	this.mId,
        apiVersion: 1,
        digiccyId: this.curDigitCard,
        smsCode: msgValue, // 
      }).then(({ data }) => {
        let result = data;
        if (result.success) {
          this.digitMsgAuth = false
          this.$router.replace('/payState?data={"is_success":"T","order_id": "' + this.orderId + '"}')
        } else if (result.errorCode === 'PCWB0001') {
          this.$toast(result.message)
        } else {
          this.digitMsgAuth = false
          this.$dialog.alert({
            message: result.message,
            confirmButtonText: ''
          }).then(() => {
            this.digitMoneyPop = true;
          })
          this.isSubmiting = false
        }
      })
    },

    /**
     * 
     */
    digitPayMsgFn() {
      this.$http.get(URL.sendSmsCaptch).then(({ data }) => {
        let result = data;
        if (result.success) {
          this.preGetTime = Date.now();
          this.resetGetSms = false
        } else {
          this.$toast(result.message)
        }
      });
    }
  }
}
</script>

